"use client"

import { FormDataContext } from '@/context/FormContext'
import { useContext } from 'react'
import 'remixicon/fonts/remixicon.css'

const Form = () => {

    const {form, changeHandler} = useContext(FormDataContext)

  return (
    <div className='flex justify-start items-center mt-14 w-full gap-8  lg:w-[50%] mx-auto p-10 rounded-lg shadow shadow-gray-300'>
      <form className='w-full'>
        <div>
      <h1 className='font-medium text-xl mb-3'><i className="ri-contacts-line mr-3 text-indigo-500"></i>Contact Information</h1>
        <div className='mt-8 grid grid-cols-1 md:grid-cols-2 gap-4 pb-9 mb-8 border-b border-gray-300'>
          <div>
            <label htmlFor="name-field" className='px-6 font-medium text-sm mb-2 block'>Full Name:</label>
            <input type="text"
                   name="name"
                   id='name-field' 
                   className='w-full border-gray-200 border rounded-2xl px-5 py-2 '       
                   placeholder='Your Name'
                   value={form.name}
                   onChange={changeHandler} 
            />
          </div>

          <div>
            <label htmlFor="email-field" className='px-6 font-medium text-sm mb-2 block'>Email:</label>
            <input type="email"
                   name="email"
                   id='email-field' 
                   className='w-full border-gray-200 border rounded-2xl px-5 py-2'
                   placeholder='Email'
                   value={form.email}
                   onChange={changeHandler} 
            />
          </div>

          <div>
            <label htmlFor="phone-field" className='px-6 font-medium text-sm mb-2 block'>Phone No.</label>
            <input type="number"
                   name="phone"
                   id='phone-field' 
                   className='w-full border-gray-200 border rounded-2xl px-5 py-2 '
                   placeholder='0333 1112222'
                   value={form.phone}
                   onChange={changeHandler} 
            />
          </div>

          <div>
            <label htmlFor="current-address-field" className='px-6 font-medium text-sm mb-2 block'>Current Address:</label>
            <input type="text"
                   name="address"
                   id='current-address-field'
                   className='w-full border-gray-200 border rounded-2xl px-5 py-2 '
                   placeholder='House No. Block Area City'
                   value={form.address}
                   onChange={changeHandler} 
            />
          </div>

        </div>
    </div>
        <button className='w-full py-3 px-4 border border-transparent rounded-lg shadow-md text-lg font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-4 focus:ring-indigo-500 focus:ring-opacity-50 transition duration-150 ease-in-out disabled:opacity-50 disabled:cursor-not-allowed'><i className="ri-send-plane-fill text-white mr-3"></i>Submit Application</button>
      </form>
    </div>
  )
}

export default Form
